 pantalla de <em>Visual Studio Code</em> con código <em>Python</em> que solicita nombres de estudiantes, los almacena en una lista y los ordena alfabéticamente, junto con la salida en la terminal donde se muestran los nombres ingresados y su ordenación ascendente."}})])]),_c('div',{staticClass:"justify-content-center",attrs:{"titulo":"Ejemplo 4: promedio de notas"}},[_c('p',[_c('b',[_c('em',[_vm._v("Prompt")])]),_vm._v(": Un estudiante está cursando 3 materias, cada materia es calificada con 3 notas, donde la nota 1 equivale al 30%, la nota 2 al 30% y la nota 3 al 40% del total del curso. Se necesita obtener el promedio por materia y el promedio general del estudiante.")]),_c('h5',{staticClass:"mb-3"},[_vm._v("Código generado en "),_c('em',[_vm._v("Python")]),_vm._v(" con "),_c('em',[_vm._v("ChatGPT")])]),_c('div',{staticClass:"tarjeta-codigo p-5 mb-5"},[_c('pre',{staticClass:"language-html mt-5",attrs:{"language":"html"}},[_c('code',[_vm._v("# Función para calcular el promedio ponderado de una materia\ndef promedio_materia():\n    print(\"\\\\nIngresa las notas de la materia:\")\n    nota1 = float(input(\"Nota 1 (30%): \"))\n    nota2 = float(input(\"Nota 2 (30%): \"))\n    nota3 = float(input(\"Nota 3 (40%): \"))\n\n    # Calcular el promedio ponderado\n    promedio = (nota1 * 0.3) + (nota2 * 0.3) + (nota3 * 0.4)\n    return promedio\n\n# Función principal para manejar el proceso\ndef promedio_general():\n    total_promedios = 0\n\n    # Calcular el promedio para las 3 materias\n    for i in range(1, 4):\n        print(\"\\\\n--- Materia\", i, \"---\")\n        promedio = promedio_materia()\n        print(\"Promedio de la materia\", i, \":\", promedio)\n        total_promedios += promedio\n\n    # Calcular el promedio general\n    promedio_general = total_promedios / 3\n    print(\"\\\\n--- Promedio General del Estudiante ---\")\n    print(\"Promedio general:\", promedio_general)\n\n# Llamar a la función principal\npromedio_general()")])])]),_c('div',{staticClass: