.png"),"alt":"Captura de pantalla de <em>Visual Studio Code</em> con código <em>Python</em> para registrar los tiempos diarios de entrenamiento de un atleta, compararlos con un récord y calcular el promedio semanal, junto con la salida en la terminal que muestra las diferencias por día y el promedio final en minutos. "}})])])]),_vm._m(28),_vm._m(29)],1)],1)
